import { replace, push } from "svelte-spa-router";
import { notify } from "./utils/notification";
export class Theme {
}
export class MyTheme {
    on_show_message(message, type) {
        notify(message, type);
    }
    on_user_prompt(message) {
        push('/user?message=' + message);
    }
    on_password_prompt(message) {
        push('/login?message=' + message);
    }
}
